// action
const GET_DATA = "select/GET_DATA";

// action creator
export const getData = res => ({ type: GET_DATA, res });

// initalState
const initalState = {};

// reducer
const select = (state = initalState, action) => {
  switch (action.type) {
    case GET_DATA:
      if (action.res.data.length === 0) {
        return { success: false, message: "검색된 내용이 없습니다." };
      }
      return { success: true, data: action.res.data };
    default:
      return state;
  }
};
// functions

// export
export default select;
